import * as React from 'react';
import { UIFarm, Home } from './pages'
import { navPath } from './navigation'
import { Switch, Route } from 'react-router-dom';
import Helment from 'react-helmet'
import './App.css';
import { title,description } from './const';

interface IAppProps {

}

class App extends React.Component<IAppProps> {
	constructor(props : IAppProps){
		super(props)
	}
	render() {
		return (
			<React.Fragment>
				<Helment
					htmlAttributes={{"leng":"en"}}
					title={title}
					titleTemplate="Truss - %s"
					defaultTitle={title}
					meta={[
						{"charSet":"utf-8"},
						{"name": "viewport", "content": "width=device-width, initial-scale=1" },
						{"name": "theme-color", "content": "#000000" },
						{"name": "description", "content": description },
						{"property": "og:type", "content": "website"},
						{"property": "og:image", "content": "%PUBLIC_URL%/ogImg.png"}
					]}
					link={[
						{"rel":"shortcut icon", "href":"%PUBLIC_URL%/favicon.ico"}
					]}

				/>
				<main>
					<Switch>
						<Route path={navPath.HOME_PAGE} component={Home} exact />
						<Route path={navPath.FARM_PAGE} component={UIFarm} />
					</Switch>
				</main>

			</React.Fragment>
		)
	}
}


export default App;
